
.loading-screen {
  position: relative;
  z-index: 1100;
  display: block;
  height: 100vh;
}
.loading-screen__inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow: hidden;
}
.loading-screen__text {
  color: white;
}



