
.collections-menu {
  right: 90px;
}
.collections-menu__title {
  margin-top: 0.5em;
}
.dialog-content > :first-child .collections-menu__title {
  margin-top: 0;
}
.collections-menu__thumb-btn {
  padding: 0;
  border: 0;
  background: none;
  cursor: pointer;
}
.collections-menu__thumb--selected {
  border: 4px solid mediumseagreen;
}



